const isContainElement = (element) => {
  return element === document.body ? false : document.body.contains(element);
};

// + openUserControl
const openUserControl = () => {
  $('.btn-open-usercontrol').on('click', () => {
    $('.user-control').toggleClass('on');
  });
};

/*
 = Side Navigation
 */
// + menu Toggle
const menuToggle = () => {
  const sideNav = $('#sideNav');
  const contentsWrap = $('.content-wrapper');
  const headerWrap = $('.header .wrap');
  const sidebarWidth = 220;
  $('.sidebar-toggle').on('click', () => {
    if ($(sideNav).hasClass('closed') === true) {
      sideNav.removeClass('closed');
      contentsWrap.css('padding-left', sidebarWidth);
      headerWrap.css('padding-left', sidebarWidth);
    } else {
      sideNav.addClass('closed');
      contentsWrap.css('padding-left', 0);
      headerWrap.css('padding-left', 0);
    }
  });
};

// + sideMenuToggle

const sideNavigation = document.querySelector('#sideNav');

const sideMenuToggle = () => {
  const sideMenuLink = document.querySelectorAll('.sidebar-menu > li > a');

  let sideMenuLinkArr = [],
    menuLength = sideMenuLink.length;

  for (let i = 0; i < menuLength; i++) {
    sideMenuLinkArr.push(sideMenuLink[i]);
  }

  Array.prototype.forEach.call(sideMenuLink, (selectedLink) => {
    selectedLink.addEventListener('click', () => {
      const extraSideMenuList = sideMenuLinkArr.filter((restLinks) => {
        return restLinks !== selectedLink;
      });

      Array.prototype.forEach.call(extraSideMenuList, (link) => {
        link.classList.remove('on');
      });
      selectedLink.classList.toggle('on');
    });
  });
};
isContainElement(sideNavigation) ? sideMenuToggle() : false;

